import {Component, DoCheck, OnInit} from '@angular/core';
import {ActivatedRoute, Params} from '@angular/router';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {validate} from 'codelyzer/walkerFactory/walkerFn';

declare var $: any;

@Component({
  selector: 'app-form-type',
  templateUrl: './form-type.component.html',
  styleUrls: ['./form-type.component.css']
})
export class FormTypeComponent implements OnInit, DoCheck {

  public type;
  date = new Date();
  year = this.date.getFullYear();
  month = (this.date.getMonth() + 1) < 10 ? '0' + (this.date.getMonth() + 1) : '' + (this.date.getMonth() + 1);
  day = this.date.getDate() < 10 ? '0' + this.date.getDate() : '' + this.date.getDate();
  currDate = this.year + '/' + this.month + '/' + this.day;
  formNumber = this.year + this.month + this.day + '01';
  holiType = ['', '', '', '', ''];
  holitype = this.holiType[0];
  public quitForm: FormGroup;
  public outForm: FormGroup;
  public holiForm: FormGroup;

  constructor(private routeInfo: ActivatedRoute, private fb: FormBuilder) {
  }

  ngOnInit() {
    this.routeInfo.params.subscribe((params: Params) => {
      this.type = params['type'];
    });
    this.quitForm = this.fb.group({
      quitDate: [this.currDate, Validators.required],
      quitReason: ['', Validators.required]
    });
    this.outForm = this.fb.group({
      outTime: ['', Validators.required],
      returnTime: ['', Validators.required],
      outFor: ['', Validators.required]
    });
    this.holiForm = this.fb.group({
      holiTime: [''],
      holiFor: ['']
    });
  }

  ngDoCheck(): void {
    if (this.type === 'out') {
      $('.timepicker').timepicker({
        showInputs: false
      });
    } else if (this.type === 'quit') {
      $('#datepicker').datepicker({
        autoclose: true,
        todayHighlight: true
      });
    }
  }

  choose() {
    $('#reservationtime').daterangepicker({
      timePicker: true,
      dateLimit: true,
      locale: {
        format: 'MM/DD/YYYY h:mm A'
      }
    });
  }
}



