import {Component, DoCheck, OnInit} from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';
import {ActivatedRoute} from '@angular/router';
import {AboutMe, UserInfo} from '../../user/user-info';
import {RegisterService} from '../../register/register.service';
import {UserInfoService} from '../../user/user-info.service';
import {StorageService} from '../../storage/storage.service';


@Component({
  selector: 'app-self-info',
  templateUrl: './self-info.component.html',
  styleUrls: ['./self-info.component.css']
})
export class SelfInfoComponent implements OnInit, DoCheck {

  myInfoForm: FormGroup;
  aboutMeForm: FormGroup;
  currUser: UserInfo;
  aboutMe: AboutMe = {id: '', edu: '', loc: '', skill: '', motto: ''};
  init = true;
  currId;
  edit = false;

  constructor(private fb: FormBuilder, private routeInfo: ActivatedRoute, private registerService: RegisterService,
              private userInfoService: UserInfoService, private storageService: StorageService) {
  }

  ngOnInit() {
    this.currUser = this.registerService.returnUserInfo();
    this.currId = this.routeInfo.snapshot.queryParams['id'];

    this.userInfoService.getAboutMe(this.currId, function (data) {
      this.aboutMe = data;
    }.bind(this));

    this.myInfoForm = this.fb.group({
      password: [this.currUser.password],
      tel: [this.currUser.tel],
      email: [this.currUser.email]
    });
    this.aboutMeForm = this.fb.group({
      edu: [''],
      loc: [''],
      skill: [''],
      motto: ['']
    });
  }

  ngDoCheck(): void {
    if (this.aboutMe.id !== '' && this.init) {
      this.aboutMeForm.reset({
        edu: [this.aboutMe.edu],
        loc: [this.aboutMe.loc],
        skill: [this.aboutMe.skill],
        motto: [this.aboutMe.motto]
      });
      this.init = false;
    }
  }

  save() {
    if (this.edit) {
      let isChange1 = false;
      let isChange2 = false;
      const myInfo = this.myInfoForm.value;
      const aboutme = this.aboutMeForm.value;
      for (const key in myInfo) {
        if (myInfo[key] !== this.currUser[key]) {
          this.currUser[key] = myInfo[key];
          isChange1 = true;
        }
      }
      for (const key in aboutme) {
        if (aboutme[key] !== this.aboutMe[key]) {
          this.aboutMe[key] = aboutme[key];
          isChange2 = true;
        }
      }
      if (isChange1) {
        this.storageService.update_data('usersInfo', [this.currUser]);
      }
      if (isChange2) {
        this.storageService.update_data('aboutMe', [this.aboutMe]);
      }
    }
  }

}



