import {Component, DoCheck, Input, OnInit} from '@angular/core';
import {ActivatedRoute, NavigationEnd, Router} from '@angular/router';
import 'rxjs/add/operator/filter';
import {getCookie, showlocalStorage} from '../storage/storage';
import {UserInfo} from '../user/user-info';
import {StorageService} from '../storage/storage.service';
import {RegisterService} from '../register/register.service';

declare var $: any;


@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.css']
})
export class EmployeeComponent implements OnInit {

  public menus: Array<Menu>;
  public currTitle;
  public childTitle;
  public currId: string;
  public currUser: UserInfo = new UserInfo('', '', '', '', '', '', '');

  constructor(private routeInfo: ActivatedRoute, private registerService: RegisterService, private router: Router) {
    this.menus = [
      new Menu('glyphicon glyphicon-home', '', ['./'], []),
      new Menu('glyphicon glyphicon-user', '', ['./self-info'], []),
      new Menu('glyphicon glyphicon-book', '', ['./user-list'], []),
      new Menu('glyphicon glyphicon-list-alt', '', ['./work-list'], []),
      new Menu('glyphicon glyphicon-file', '', ['./form-apply'], [
        new Menu('', '', ['./form-apply/form-type', 'quit'], []),
        new Menu('', '', ['./form-apply/form-type', 'out'], []),
        new Menu('', '', ['./form-apply/form-type', 'holiday'], [])
      ])
    ];
  }

  ngOnInit() {
    const user = this.registerService.returnUserInfo();
    if (!user) {
      this.router.navigateByUrl('/login');
    } else {
      this.currUser = user;
      this.currId = user.id;
    }
    if ($.AdminLTE.controlSidebar) {
      $.AdminLTE.controlSidebar.activate();
    }
    this.currTitle = this.menus[0].title;
  }

  changeTitle(menu: Menu) {
    this.currTitle = menu.title;
    this.childTitle = '';
  }

  changeMenu(menu: Menu) {
    this.childTitle = menu.title;
  }
}

export class Menu {
  constructor(public icon: string,
              public title: string,
              public link: Array<string>,
              public children: Array<Menu>) {
  }
}



