import {Injectable} from '@angular/core';
import {AboutMe, EmployeeInfo, UserInfo, UserToDoList} from './user-info';
import {StorageService} from '../storage/storage.service';

@Injectable()
export class UserInfoService {
  public employeesInfo: Array<EmployeeInfo>;
  public usersInfo: Array<UserInfo>;
  public aboutMe: Array<AboutMe>;
  isDone = false;

  constructor(private storageService: StorageService) {
    this.employeesInfo = [
      new EmployeeInfo('s15801362', '', ''),
      new EmployeeInfo('s15801363', '', ''),
      new EmployeeInfo('s15801364', '', ''),
      new EmployeeInfo('s15801365', '', ''),
      new EmployeeInfo('s15801366', '', ''),
      new EmployeeInfo('s15801367', '', ''),
      new EmployeeInfo('s15801368', '', ''),
      new EmployeeInfo('s15801369', '', ''),
      new EmployeeInfo('s15801370', '', ''),
      new EmployeeInfo('s15801371', '', ''),
      new EmployeeInfo('s15801372', '', ''),
      new EmployeeInfo('s15801373', '', ''),
      new EmployeeInfo('s15801374', '', '')
    ];
    this.usersInfo = [
      new UserInfo('s15801362', '', '', '15050490207', 'elvira0702@163.com', '5201314', 'assets/img/user1-128x128.jpg'),
      new UserInfo('s15801363', '', '', '15050490200', '111@163.com', '5201314', 'assets/img/user3-128x128.jpg'),
      new UserInfo('s15801364', '', '', '15050490201', '222@163.com', '5201314', 'assets/img/user4-128x128.jpg'),
      new UserInfo('s15801365', '', '', '15050490202', '333@163.com', '5201314', 'assets/img/user5-128x128.jpg'),
      new UserInfo('s15801366', '', '', '15050490203', '444@163.com', '5201314', 'assets/img/user6-128x128.jpg'),
      new UserInfo('s15801367', '', '', '15050490204', '555@163.com', '5201314', 'assets/img/user7-128x128.jpg'),
      new UserInfo('s15801368', '', '', '15050490205', '666@163.com', '5201314', 'assets/img/user8-128x128.jpg'),
      new UserInfo('s15801369', '', '', '15050490206', '777@163.com', '5201314', 'assets/img/user6-128x128.jpg'),
      new UserInfo('s15801370', '', '', '15050490207', '888@163.com', '5201314', 'assets/img/user1-128x128.jpg'),
    ];
    this.aboutMe = [
      new AboutMe('s15801362', '', '', '', ''),
      new AboutMe('s15801363', '', '', '', ''),
      new AboutMe('s15801364', '', '', '', ''),
      new AboutMe('s15801365', '', '', '', ''),
      new AboutMe('s15801366', '', '', '', ''),
      new AboutMe('s15801367', '', '', '', ''),
      new AboutMe('s15801368', '', '', '', ''),
      new AboutMe('s15801369', '', '', '', ''),
      new AboutMe('s15801370', '', '', '', ''),
    ];
  }

  buildStorage() {
    const index: Array<any> = [['dep', false]];
    const table: Array<any> = [['employeesInfo', [], this.employeesInfo],
      ['usersInfo', index, this.usersInfo], ['toDoList', [['userId', false]]],
      ['aboutMe', [], this.aboutMe]];
    this.storageService.openDataBase(table, function () {
      this.isDone = true;
    }.bind(this));
  }

  getUsersByDep(dep: string, callback) {
    if (dep && this.isDone) {
      let userList = [];
      this.storageService.get_data('usersInfo', ['index', 'dep', dep], function (data) {
        userList = data;
        callback(userList);
      });
    }
  }

  getUserToDoList(userId: string, callback) {
    if (userId && this.isDone) {
      let toDoList = [];
      this.storageService.get_data('toDoList', ['index', 'userId', userId], function (data) {
        toDoList = data;
        callback(toDoList);
      });
    }
  }

  getAboutMe(id: string, callback) {
    if (id && this.isDone) {
      let aboutMe;
      this.storageService.get_data('aboutMe', ['key', '', id], function (data) {
        aboutMe = data;
        callback(aboutMe);
      });
    }
  }

}





