import {Component, OnInit} from '@angular/core';
import {UserInfo} from '../../user/user-info';
import {UserInfoService} from '../../user/user-info.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.css']
})
export class UserListComponent implements OnInit {

  public isList;
  public departments = ['', '', '', '', '', ''];

  public curDep = '';
  public users: Array<UserInfo> = [];
  userId: string;

  constructor(private userInfoService: UserInfoService, private routeInfo: ActivatedRoute) {
  }

  ngOnInit() {
    this.isList = true;
    this.userId = this.routeInfo.snapshot.queryParams['id'];
    if (this.userId) {
      this.userInfoService.getUsersByDep(this.curDep, function (data) {
        this.users = data;
      }.bind(this));
    }
  }

  update() {
    this.userInfoService.getUsersByDep(this.curDep, function (data) {
      this.users = data;
    }.bind(this));
  }
}




