import {Injectable} from '@angular/core';
import {Http} from '@angular/http';
import {FormList} from '../../user/user-info';
import 'rxjs/RX';
import {Observable} from 'rxjs/Observable';

@Injectable()
export class FormService {

  constructor(public http: Http) {
  }

  getFormLists(): Observable<FormList[]> {
    return this.http.get('/api/formList').map(res => res.json());
  }

  getFormList(id: string): Observable<FormList> {
    return this.http.get('/api/formList/' + id).map(res => res.json());
  }

}



